h1{
    text-align: center;
}
img{
    margin-top:10px ;
}

.flex{
    
    
    width: 60% ;
    margin: 50px auto;
    text-align: center;
    display: grid;
    grid-template-rows: 250px 200px 200px 60px ;
    grid-template-columns: repeat(2, 1fr) 200px;
    
}

.head img{
    cursor: pointer;
    
}

@media screen and (max-width:600px){
   .flex{
       
       width: 100px;
       display: block;
       margin-bottom: 20px;
   }
   .head{
       margin: 30px;
       text-align: center;
   }
   .head img{
    text-align: center;
      
   }
}